<template>
<el-container v-loading='isloading'>
 <el-header class = 'header' style="background:#EDF2FC;">
   <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item>骑手考勤</el-breadcrumb-item>
    </el-breadcrumb>
 </el-header>
<el-main>
  <el-form class="small-space" :model="mainForm" ref="mainForm" :rules='rolus' label-position="right" label-width="120px" size='small'>
    <el-form-item label="城市" prop='CityName'>
      <el-select v-model="mainForm.CityName"  placeholder="请选择" :clearable = 'true'>
         <el-option
          v-for="item in cityForm"
          :key="item.GroupID"
          :label="item.Name"
          :value="item.GroupID">
        </el-option>
      </el-select>
    </el-form-item>
     <!-- <el-form-item label="日期" prop="Daytiems">
      <el-date-picker
          v-model="mainForm.Daytiems"
          type="date"
          placeholder="选择日期"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd">
      </el-date-picker>
     </el-form-item> -->
     <el-form-item label="选择日账单" prop='DeliveryFeeBillCsvPath' class="uploadPathForm">
         <el-col>
           <el-input v-model.trim="mainForm.DeliveryFeeBillCsvPath" auto-complete="off" placeholder="请选择日账单" ref="DeliveryFeeBillCsvPath" class="input-with-select" :disabled="true">
            <el-button slot="append" icon="el-icon-search" @click="handledeliveryFeeBillCsvPath"></el-button>
           </el-input>
        </el-col>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="mainCreate('mainForm')" class="userdButton">考勤</el-button>
    </el-form-item>
  </el-form>
</el-main>
</el-container>
</template>
<script>
import api from '@/utils/api'
const fileDownload = require('js-file-download')
export default {
  data () {
    return {
      isloading: false,
      cityForm: [],
      mainForm: {
        DeliveryFeeBillCsvPath: '',
        CityName: '',
        RiderEleIDColName: '',
        // Daytiems: '2017-10-15',
        NameColName: '',
        RiderPickUpTimeColName: '',
        TeamIDColName: '',
        TeamNameColName: ''
      },
      rolus: {
        CityName: [
          {required: true, message: '城市不能为空', trigge: 'change,blur'}
        ],
        // Daytiems: [
        //   {required: true, message: '日期不能为空', trigge: 'change,blur'}
        // ],
        DeliveryFeeBillCsvPath: [
          {required: true, message: '日账单不能为空', trigge: 'change,blur'},
          {pattern: /\.(?:csv)$/i, message: '请输入正确的文件(csv)', trigge: 'change,blur'}
        ]
      }
    }
  },
  mounted: function () {
    // const date = new Date()
    // const times = date.getTime() - 24 * 60 * 60 * 1000 * 2
    // const oldtime = new Date(times).toLocaleString().substr(0, 10).replace(/\//g, '-')
    // this.mainForm.Daytiems = oldtime
    this.getCity()
  },
  methods: {
    getCity () {
      this.isloading = true
      api.getCitys().then(response => {
        this.isloading = false
        this.cityForm = response.data.list
        // console.log('getcity' + JSON.stringify(response.data.list))
      }, error => {
        this.showErrorMessage(error.message)
      })
    },
    handledeliveryFeeBillCsvPath () {
      this.popupCKFinder('DeliveryFeeBillCsvPath')
    },
    // popupCKFinder
    popupCKFinder (name) {
      const _this = this
    /* eslint-disable no-undef */
      try {
        CKFinder.popup({
          chooseFiles: true,
          width: 800,
          height: 600,
          onInit: function (finder) {
            finder.on('files:choose', function (evt) {
              const file = evt.data.files.first()
              _this.mainForm[name] = file.getUrl()
            })
            finder.on('file:choose:resizedImage', function (evt) {
              _this.mainForm[name] = evt.data.resizedUrl
            })
          }
        })
      } catch (e) {
        console.log(e.message)
      }
    },
    mainCreate (formName) {
      this.$refs.mainForm.validate((valid) => {
        if (valid) {
          const params = this.mainForm
          this.isloading = true
          api.importAttendance(params).then(response => {
            this.isloading = false
            console.log(this.mainForm.CityName + '考勤情况返回表.xlsx')
            fileDownload(response.data, this.mainForm.CityName + '考勤情况返回表.xlsx')
            this.mainForm.CityName = ''
          }, error => {
            this.isloading = false
            this.showErrorMessage(error.message)
          })
        } else {
          this.showErrorMessage('请填写必填项')
        }
      })
    },
    showErrorMessage (message) {
      this.$message({
        message: message,
        type: 'error'
      })
    }
  }
}
</script>
<style scodived lang="scss">
 .uploadPathForm {
   position: relative;
   width: 500px;
 }
</style>


